import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';
import {ModuleWithProviders, NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';

import {AdaptAlertAnimatedModule} from '../alert-animated/index';
import {AdaptModalModule} from '../modal/index';
import {AdaptCloseModule} from '../close/index';
import {AdaptDockedPanelModule} from '../docked-panel/index';
import {AdaptDropdownModule} from '../dropdown/index';
import {AdaptMenuModule} from '../menu/index';
import {AdaptPopoverModule} from '../popover/index';
import {AdaptProfileModule} from '../profile/index';
import {AdaptRxSearchModule} from '../rx/rx-search/index';
import {AdaptRxTextfieldModule} from '../rx/rx-textfield/index';
import {AdaptTooltipModule} from '../tooltip/index';

import {AdaptNavActionsComponent, AdaptNavActionsItemComponent, AdaptNavActionsSubitemComponent} from './nav.actions.component';
import {AdaptNavTextSearchPipe, AdaptNavHamburgerPipe} from './navigation.pipe';
import {AdaptNavigationComponent} from './navigation.component';
import {AdaptNavigationService} from './navigation.service';
import {AdaptNavBadgeCounterComponent} from './badge-counter/nav-badge-counter.component';
import {AdaptNavActionsSwictherComponent} from './nav-actions-switcher/nav-actions-swicther.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule,
    AdaptAlertAnimatedModule,
    AdaptModalModule,
    AdaptCloseModule,
    AdaptDockedPanelModule,
    AdaptDropdownModule,
    AdaptMenuModule,
    AdaptPopoverModule,
    AdaptProfileModule,
    AdaptRxSearchModule,
    AdaptRxTextfieldModule,
    AdaptTooltipModule
  ],
  exports: [AdaptNavigationComponent],
  declarations: [
    AdaptNavActionsComponent,
    AdaptNavActionsItemComponent,
    AdaptNavActionsSubitemComponent,
    AdaptNavBadgeCounterComponent,
    AdaptNavHamburgerPipe,
    AdaptNavTextSearchPipe,
    AdaptNavigationComponent,
    AdaptNavActionsSwictherComponent
  ],
  providers: [AdaptNavigationService]
})
export class AdaptNavigationModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptNavigationModule> {
    return {
      ngModule: AdaptNavigationModule
    };
  }
}

export {AdaptNavigationComponent} from './navigation.component';
export {AdaptNavActionsComponent, AdaptNavActionsItemComponent, AdaptNavActionsSubitemComponent} from './nav.actions.component';
export {AdaptNavigationService} from './navigation.service';
export * from './navigation.config';
